.card {
  margin: auto;
  max-width: 83%;
  border-radius: 0.3125rem;
  overflow: hidden;
  display: flex;
  flex-direction: column;
}
@media (max-width: 39.9375em) {
  .card {
    margin-top: 4.375rem;
  }
}
@media (min-width: 40em) {
  .card {
    max-width: 65%;
    min-width: 34.375rem;
    border-radius: 0.4375rem;
    flex-direction: row;
    flex-wrap: wrap;
  }
}
@media (min-width: 71.875em) {
  .card {
    max-width: 45%;
    min-width: 37.5rem;
  }
}
.card__section {
  padding: 1.5625rem;
  text-align: left;
}
@media (min-width: 40em) {
  .card__section {
    padding: 2.1875rem;
  }
}
@media (min-width: 71.875em) {
  .card__section {
    padding: 2.5rem;
  }
}
.card__join {
  background-color: white;
}
.card__join h1 {
  color: #2ab2af;
  margin-bottom: 1.5625rem;
}
.card__join h3 {
  color: #c0df34;
  margin-bottom: 1.5625rem;
  line-height: 1.25rem;
}
.card__join p {
  color: #98a6bd;
  line-height: 1.625rem;
}
@media (min-width: 40em) {
  .card__join {
    flex: 100%;
  }
}
.card__subs {
  background-color: #2ab2af;
  color: white;
}
@media (min-width: 40em) {
  .card__subs {
    flex: 50%;
  }
}
.card__subs h3 {
  margin-bottom: 1.25rem;
}
.card__subs__price {
  margin-bottom: 0.625rem;
  display: flex;
  align-items: center;
  gap: 0.9375rem;
}
.card__subs__price--dollar {
  font-size: 2rem;
  font-weight: 700;
}
.card__subs__price--permonth {
  font-size: 1.0625rem;
  font-weight: 400;
  color: #e5eff5;
}
.card__subs p {
  font-size: 0.9375rem;
  margin-bottom: 1.5625rem;
}
.card__subs__btn {
  width: 100%;
  border-radius: 0.3125rem;
  background-color: #c0df34;
  text-align: center;
  padding: 0.9375rem;
}
.card__subs__btn a, .card__subs__btn a:hover, .card__subs__btn a:active {
  font-size: 0.9375rem;
  font-weight: 700;
  text-decoration: none;
  color: white;
}
.card__whyus {
  background-color: rgba(42, 178, 175, 0.8);
}
@media (min-width: 40em) {
  .card__whyus {
    flex: 50%;
  }
}
.card__whyus h2 {
  color: white;
  margin-bottom: 1.25rem;
}
.card__whyus ul {
  color: #e5eff5;
  list-style-type: none;
  line-height: 1.25rem;
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
  font-family: "Karla", sans-serif;
  font-size: 0.8125rem;
  background-color: #e5eff5;
}
@media (min-width: 40em) {
  body {
    font-size: 1rem;
    display: flex;
  }
}
body * h1 {
  font-size: 1.25rem;
}
@media (min-width: 40em) {
  body * h1 {
    font-size: 1.5rem;
  }
}
body * h2 {
  font-size: 1.0625rem;
}
@media (min-width: 40em) {
  body * h2 {
    font-size: 1.25rem;
  }
}
body * h3 {
  font-size: 0.9375rem;
}
@media (min-width: 40em) {
  body * h3 {
    font-size: 1.0625rem;
  }
}